// You can delete this file if you're not using it
exports.shouldUpdateScroll = ({prevRouterProps, routerProps}) => {
    if(
        (routerProps.location.pathname === "/about/" && prevRouterProps?.location.pathname !== "/about/") ||
        (routerProps.location.pathname === "/project/connect4/" && prevRouterProps?.location.pathname !== "/project/connect4/")
    ) {
        let originalClientHeight = document.body.clientHeight
        let waitForChange = setInterval(() => {
            if (document.body.clientHeight !== originalClientHeight) {
                window.scrollTo(0, 0)
                clearInterval(waitForChange)
            }
        }, 1)
        return false
    }
    return true
}